<template>
	<div :style="{paddingBottom: fucHeight(height)}" class="cdiv">
		<div class="cinner">
			<slot></slot>
		</div>
	</div>
</template>

<script>
export default {
    props: ['height','className'],
    methods: {
    	fucHeight(h) {
    		return (h * 10) / 75 + "%";
    	}
    }
  }	
</script>

<style lang="stylus">
	.cdiv
		overflow: hidden
		height 0;
		position: relative
	.cinner
		position: absolute
		width 100%;
		height 100%;
</style>


