<template>
	<div class="home">
    <c-div :height="400">
  		<swipe class="swiper_home" :speed="600" >
  			<swipe-item class="slide1" v-for="banner in homeData.topimage_ary"> <img :src="banner" alt=""></swipe-item>
  		</swipe>
    </c-div>
		<div class="inner bar-title dpr-bottom">
			<h1>{{homeData.user_name}}</h1>
			<p></p>
		</div>
		<h1>Home</h1>
		<router-link :to="{ name: 'hello'}">to hello</router-link>
		<nav-bar></nav-bar>	
	</div>
</template>

<script>
  import {mapState, mapGetters} from 'vuex';
  import navBar from '../components/navBar.vue';
  import { Swipe, SwipeItem } from 'vue-swipe';
  import cDiv from '../components/div.vue';

  export default {
    data () {
      return {
        homeData: {}
			}
		},
    computed: {
      ...mapGetters({
        userInfo: 'getUserInfo'
      })
    },
    methods: {
      async getHomeData () {
        let data = new FormData()
        data.append('member_id', this.userInfo.member_id)
        var myHeaders = new Headers();
        myHeaders.append('dataType', 'json');
        const response = await fetch(this.$api.home + '&member_id=' + this.userInfo.member_id);
        this.homeData = (await response.json()).datas;
				console.log(this.homeData)
//        this.$store.commit('UPDATE_LOADING', false);
        this.$store.dispatch('UPDATE_LOADING', false);
      }
		},
    mounted () {
//      this.$store.commit('UPDATE_LOADING', false);
      this.getHomeData()
    },
    components: {
    	navBar,
      Swipe,
      SwipeItem,
      cDiv
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "../../node_modules/vue-swipe/dist/vue-swipe.css";
	.swiper_home {
		height: 100%;
		position: relative;
		overflow: hidden;
	}
	.bar-title
		padding-bottom 1px
		h1
			margin-bottom:0;
			height 42px
		p
			margin-top:0
</style>


